<template>
    <div 
        class="detail-item"
    >
        <div 
            class="background"
            :class="{
                'with-shadow': props.invertShadow !== null,
                'invert-shadow': !!props.invertShadow
            }"
            :style="{
                opacity: backgroundOpacity,
                'aspect-ratio': props.aspectRatio,
                height: props.previewHeight ? props.previewHeight + 'px' : 'auto'
            }"
        >
            <slot name="background"></slot>
        </div>

        <div class="box">
            <div 
                class="preview"
                :style="{
                    opacity: 1,
                    'aspect-ratio': props.aspectRatio,
                    height: props.previewHeight ? props.previewHeight + 'px' : 'auto'
                }"
            >
                <div 
                    class="wrapper"
                    :style="{ opacity: wrapperOpacity }"
                >
                    <div class="toolbar">
                        <slot name="toolbar"></slot>
                    </div>

                    <slot name="preview"></slot>
                </div>
            </div>

            <div 
                class="detail" 
                ref="detail"
            >
                <slot name="detail"></slot>
            </div>
        </div>
  </div>
</template>

<script setup>
    // import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import { ref, reactive, computed, onMounted, onBeforeUnmount } from "vue";
    import stores from '@/stores';

    const others = stores.others();

    const props = defineProps({
        previewHeight: { type: Number, default: 384 },
        aspectRatio: { type: [String, null], default: null },
        invertShadow: { type: [Boolean, null], default: null },

    });

    const data = reactive({
        mode: 'preview'
    });


    const emit = defineEmits(['mode']);

    const detail = ref(null);

    const scrollTop = ref(0);
    const scrollDirection = ref(0);


    const progress = computed(() =>
        Math.min(1, scrollTop.value / (props.previewHeight || detail?.value?.clientWidth))
    );

    // Opacity
        const wrapperOpacity = computed(() => 1 - progress.value);
        const backgroundOpacity = computed(() => 1 - progress.value);

    let lastY = 0;
    const onScroll = (e) => {
        const y = e.target.scrollTop;
        scrollDirection.value = y - lastY;
        lastY = y;

        scrollTop.value = y;
        updateMode();
    }

    const updateMode = () => {
        const newMode = progress.value >= 1 ? "detail" : "preview";
        if (newMode !== data.mode) {
            data.mode = newMode;
            emit('mode', newMode);
        }
    }


    document.body.classList.add('with-tg-header');
    onMounted(() => {
        document.querySelector('.page').addEventListener('scroll', onScroll);
    });

    onBeforeUnmount(() => {
        document.body.classList.remove('with-tg-header');
        document.querySelector('.page').removeEventListener('scroll', onScroll);
    });
</script>


<style scoped lang="scss">
    .detail-item {
        position: relative;
        // height: 100%;
        width: 100%;
        // overflow-y: auto;
        // scroll-behavior: smooth;
        // min-height: 100%;
        // position: fixed;
        // top: 0;
        // left: 0;
        // right: 0;
        // bottom: var(--nav-height);

        .background {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;

            &.with-shadow {
                &:after {
                    content: "";
                    inset: 0;
                    position: absolute;
                    pointer-events: none;
                    background: linear-gradient(101deg, var(--white-5) 41%, var(--white-90) 89%);
                }
            }

            &.invert-shadow {
                &:after {
                    background: linear-gradient(101deg, var(--dark-5) 75%, var(--dark-80) 100%)
                }
            }
        }

        .box {
            min-height: 100%;
            display: flex;
            flex-direction: column;
        }
        .preview {
            position: relative;
            top: 0;
            overflow: hidden;
            pointer-events: none;
            z-index: 1;

            .toolbar {
                position: absolute;
                top: calc(var(--tg-safe-area-inset-top, 0px) + var(--tg-content-safe-area-inset-top, 0px) + 12px);
                right: -4px;
                display: flex;
                flex-direction: column-reverse;
                align-items: center;
                gap: 0px;
            }

            .wrapper {
                position: absolute;
                inset: 0;
                padding: 16px;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                z-index: 2;
                pointer-events: auto;
                gap: 16px;
            }
        }

        .detail {
            flex: 1;
            display: flex;
            flex-direction: column;
            background: var(--white-500);
            // min-height: 200vh;
            position: relative;
            padding: 16px 0;
            gap: var(--block-gap);

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 36px;
                transform: translateY(-100%);
                background: linear-gradient(to bottom, var(--white-0) 0%, var(--white-500) 100%);
                pointer-events: none;
            }
        }
    }
</style>
